body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    height:100vh;
    background: #ebe7e0;
}

.Invitation{
    padding:30px
}

.Envelope{
    margin: 0 auto;
    margin-top:5%;
    background: #ffffff;
    width:640px;
    height:400px;
    border:5px solid #B4BEC8;
    position:relative;
    border-radius:10px;
    overflow:hidden;
    cursor:pointer;
}

.Envelope:before{
    border-bottom:5px solid #B4BEC8;
    position:absolute;
    display:block;
    content:"";
    top:100px;
    left:-40px;
    width:400px;
    height:8px;
    -webkit-transform:rotate(35deg);
    transform:rotate(35deg);
}

.Envelope:after {
    border-bottom: 5px solid #B4BEC8;
    position: absolute;
    display: block;
    content: "";
    top: 100px;
    right: -40px;
    width: 400px;
    height: 8px;
    -webkit-transform: rotate(-35deg);
    transform: rotate(-35deg);
}

.Envelope h5{
    bottom: -10%;
    left: 2%;
    font-weight: 100;
    font-size: 2em;
    position: absolute;
    font-family: Rochester;
    color:#858c92;
}

/* Letter */
.Letter-Container{
    background-image: url(/static/media/Letter.c48075bf.png);
    height: 933px;
    width: 786px;
    background-size: 100% 100%;
    display:block;
    position: relative;
    font-family: Rochester;
    font-size: 42px;
    font-weight: 400;
    color: rgb(84, 117, 95);
    margin: 0 auto;
}

.Letter-Container .bottomHalf{
    position:absolute;
    bottom: 0;
    height:52%;
    width:100%;
    text-align:center;
}

.primary{
    color: rgb(84, 117, 95);
}

.secondary{
    color: rgb(179, 124, 50);
}

.small{
    font-size: 0.5em;
}

.big{
    font-size: 1.2em;
}

p.greeting{
    font-family: Rokkitt;
    font-weight: 300;
    margin-bottom: -45px;
}

p.title{
    font-family: Rochester;
    font-weight: 400;
}

p.time{
    font-family: Rokkitt;
    font-weight: 300;
}

p.address{
    font-family: Rokkitt;
    font-weight: 300;
}
/*Animations*/
.fade-enter{
    opacity: 0.01;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.fade-appear{
    opacity: 0.01;
}

.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.fade-leave{
    opacity: 1;
}

.fade-leave.fade-leave-active {
    opacity: 0.01;
    transition: opacity 500ms ease-in;
}
.button {
    background-color: #fcf7ed; /* Green */
    border: none;
    color: black;
    padding: 7px 17px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-family: Rokkitt;
    font-size: 0.5em;
    cursor:pointer;
}

.button:hover{
    background-color: #f5efe2;
}


/*Details*/
.Details-Container{
    height: auto;
    width: 618px;
    padding: 20px 50px 22px 50px;
    background-color:#f4ebde;
    display:block;
    position: relative;
    font-family: Rokkitt;
    font-size: 30px;
    color: rgb(84, 117, 95);
    text-align:center;
    margin: 0 auto;  /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */  /* Firefox 3.5 - 3.6 */
    box-shadow:         3px 3px 6px 2px #989590;  /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
}

.flowers{
    width:100%;
}
.schedule{
    width: 50%;
    float:right;
    text-align:right;
}

.subtitle{
    font-family:Rochester;
    font-size:1em;
    margin-bottom:-10px;
}

.details{
    text-align:left;
    width: 50%;
    float:left;
}

.schedule p, .details p{
    font-size:0.6em;
}

.clear{
    clear:both;
}

.footer{
    margin-top:2%;
    text-align:center;
    width: 100%;
}


hr {
    border: 0;
    clear:both;
    display:block;
    width: 100%;
    background-color:rgb(175, 195, 182);
    height: 1px;
}

/* Smartphones (portrait) ----------- */
@media only screen
and (min-device-width: 320px)
and (max-device-width: 736px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: portrait) {
    .Envelope {
        width: 405px;
        height: 227px;
    }
    .Envelope:before{
        top:18px;
        left: -146px; width: 384px;
    }

    .Envelope:after {
        top:18px;
        left: 163px; width: 388px;
    }
    .Envelope h5 {
        font-size:1em;
    }
    .Letter-Container{
        display: block;
        margin: 0 auto;
        position: relative;
        width: 624px;
        height: 790px;
        background-image: url(/static/media/Letter_Mobile.c1e54ab7.png);
    }

    .Details-Container{
        height: auto;
        width: 618px;
        padding: 20px 50px 22px 50px;
        background-color:transparent;
        display:block;
        position: relative;
        font-family: Rokkitt;
        font-size: 30px;
        color: rgb(84, 117, 95);
        text-align:center;
        margin: 0 auto;  /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */  /* Firefox 3.5 - 3.6 */
        box-shadow:         none;  /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
    }
}

/* Landscape */
@media only screen
and (min-device-width: 320px)
and (-webkit-min-device-pixel-ratio: 2)
and (max-device-width: 736px)
and (orientation: landscape) {
    .Envelope {
        width: 400px;
        height: 250px;
    }
    .Envelope:before{
        top:53px;
        left:-40px;
        width:268px;
    }

    .Envelope:after {
        top:53px;
        width:268px;
    }

    .Envelope h5{
        bottom: -16%;
    }

}

.modal{
     position: absolute;
     top: 40px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
     border: none;
     background: none;
     border-radius: 0px;
    outline: none;
    padding: 20px;
    width: 1000px;
}
.card-form {
    border-radius: 10px;
    background: white;
    box-shadow: 0 -2px 19px 0 rgba(0, 0, 0, 0.3), 0 13px 17px 0 rgba(0, 0, 0, 0.15);
}
.card-form .form-title {
    font-family: Rochester;
    width: 100%;
    display: flex;
    align-items: center;
    color: #576467;
    justify-content: center;
    height: 80px;
    font-size: 40px;
    font-weight: bold;
    background: #f3eadd;
    border-radius: 10px 10px 0 0;
}
.card-form .form-body {
    padding: 10px;
}
.card-form .form-body .row {
    display: flex;
    justify-content: space-around;
    padding: 10px;
}

.card-form .form-body .row.header{
    justify-content: flex-start;
    padding-left: 30px;
    padding-bottom:0px;
}

.card-form .form-body .row.header h2{
    color: #576366;
    text-align:left;
}

.card-form .form-body .row input, .card-form .form-body .row textarea{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    margin: 0 15px;
    height: 35px;
    padding: 5px 15px;
    border-radius: 5px;
    outline: none;
    border: none;
    background: #e8ebed;
    color: #576366;
    font-size: 14px;
}

.card-form .form-body .row textarea{
    padding-top:13px;
}

.card-form .rule {
    height: 2px;
    background: #e8ebed;
    margin: 0px 35px;
}
.card-form .form-footer {
    margin: 0 25px 15px 25px;
    padding: 15px 10px;
}
.card-form .form-footer a {
    font-family: Rokkitt;
    color: #5a6568;
    background: #f3eadd;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    border: none;
    border-radius: 5px;
    padding: 5px 15px;
    margin-right: 10px;
    font-size: 19px;
    cursor:pointer;
}

.card-form .form-footer a:hover{
    background: #eae3d8;
}

.card-form .form-footer span {
    margin-left: 8px;
}


.form-checkbox
{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: inline-block;
    position: relative;
    background-color: #f1f1f1;
    color: #666;
    top: 10px;
    height: 30px;
    width: 30px;
    border: 0;
    border-radius: 3px;
    cursor: pointer;
    margin-right: 7px;
    outline: none;
}
.form-checkbox:checked::before
{
    position: absolute;
    font: 13px/1 'Open Sans', sans-serif;
    left: 11px;
    top: 7px;
    content: '\2143';
    -webkit-transform: rotate(40deg);
            transform: rotate(40deg);
}
.form-checkbox:hover
{
    background-color: #f7f7f7;
}
.form-checkbox:checked
{
    background-color: #f1f1f1;
}
label
{
    font: 300 16px/1.7 'Open Sans', sans-serif;
    color: #666;
    cursor: pointer;
}

.card-form .form-body .checkbox_row {
    padding-left: 25px;
}

@media only screen
and (min-device-width: 320px)
and (max-device-width: 736px)
and (-webkit-min-device-pixel-ratio: 2) {
    .modal{
        width: 564px;
    }
}

@media only screen
and (min-device-width: 320px)
and (max-device-width: 736px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: landscape) {
    .modal{
        overflow-y: auto;
        max-height: 150vh;
        top: 0px;
    }
}

@media only screen
and (min-device-width: 768px)
and (max-device-width: 1024px)
and (-webkit-min-device-pixel-ratio: 2) {
    .modal{
        width: 694px;
        left: 47%;
    }

}
